
.TicTacToe{
    margin: 10px auto;
    max-width: 600px;
    text-align: center;
    box-sizing: border-box;
}
.row1,.row2,.row3{
    display: flex;
    align-items: center;
    justify-items: center;
}
.cover{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.Players{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 10px;
}
.player1,.player2{
    height: 40px;
    width: 60px;
    padding: 10px;
    margin: 10px;
    font-weight: bold;
    font-size: 30px;
    text-align: center;
    border-radius: 10px;
}
.player1.active{
    border-bottom: 10px solid blue;
}
input{
    margin:10px;
}